.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}


.banner{
  -webkit-animation: zoom-in-out 3s infinite 0s ease-in-out;
          animation: zoom-in-out 3s infinite 0s ease-in-out;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


@-webkit-keyframes zoom-in-out{
    0%{
        -webkit-transform: rotate(xx) scale(1.5);
                transform: rotate(xx) scale(1.5);
        }
    25%{
        -webkit-transform: scale(1.2);
                transform: scale(1.2);
        }
    50%{
        -webkit-transform: scale(1);
                transform: scale(1);
        }
    75%{
        -webkit-transform: scale(1.2);
                transform: scale(1.2);
        }
    100%{
        -webkit-transform: rotate(xx) scale(1.5);
                transform: rotate(xx) scale(1.5);
        }
}


@keyframes zoom-in-out{
    0%{
        -webkit-transform: rotate(xx) scale(1.5);
                transform: rotate(xx) scale(1.5);
        }
    25%{
        -webkit-transform: scale(1.2);
                transform: scale(1.2);
        }
    50%{
        -webkit-transform: scale(1);
                transform: scale(1);
        }
    75%{
        -webkit-transform: scale(1.2);
                transform: scale(1.2);
        }
    100%{
        -webkit-transform: rotate(xx) scale(1.5);
                transform: rotate(xx) scale(1.5);
        }
}
